<script>
  let yOffset = 0;
  let prevScrollHeight = 0;
  $: currentYOffset = yOffset - prevScrollHeight;
  let sceneNow = 0;
  let text = "";
  let time = 0;
  const scene = [
    {
      scrollHeight: 0,
      heightNum: 2,
      background: "images/mountain.jpg",
      el: {
        conatiner: undefined,
        sticky: undefined,
      },
    },
    {
      scrollHeight: 0,
      heightNum: 3,
      background: "images/preview.png",
      galleries: [
        {
          img: "images/vintage-sewing-tools.jpg",
          text:
            "Lorem ipsum, dolor sit amet consectetur adipisicing elit. Et officia alias eaque libero enim possimus tenetur velit? Deleniti ea esse consectetur minus sunt, illo porro, qui explicabo, sit eaque nobis molestiae enim non repudiandae earum. Dolore fugit ratione odio asperiores?",
        },
        {
          img: "images/filling-wine-glasses-on-counter.jpg",
          text:
            "Lorem ipsum, dolor sit amet consectetur adipisicing elit. Et officia alias eaque libero enim possimus tenetur velit? Deleniti ea esse consectetur minus sunt, illo porro, qui explicabo, sit eaque nobis molestiae enim non repudiandae earum. Dolore fugit ratione odio asperiores?",
        },
        {
          img: "images/sunset.jpg",
          text:
            "Lorem ipsum, dolor sit amet consectetur adipisicing elit. Et officia alias eaque libero enim possimus tenetur velit? Deleniti ea esse consectetur minus sunt, illo porro, qui explicabo, sit eaque nobis molestiae enim non repudiandae earum. Dolore fugit ratione odio asperiores?",
        },
      ],
    },
    {
      scrollHeight: 0,
      heightNum: 1,
    },
    {
      scrollHeight: 0,
      heightNum: 4,
    },
  ];
  const scrollLoop = () => {
    console.log(yOffset, sceneNow, currentYOffset);
    prevScrollHeight = 0;

    for (let i = 0; i < sceneNow; i++) {
      prevScrollHeight += scene[i].scrollHeight;
    }
    if (yOffset > scene[sceneNow].scrollHeight + prevScrollHeight) {
      sceneNow++;
    }
    if (yOffset < prevScrollHeight) {
      if (sceneNow == 0) return;
      sceneNow--;
    }
    playAnimation();
  };
  const setLayout = () => {
    for (let i = 0; i < scene.length; i++) {
      scene[i].scrollHeight = window.innerHeight * scene[i].heightNum;
    }
    scrollLoop();
  };
  const playAnimation = () => {
    switch (sceneNow) {
      case 0:
        if (1500 > yOffset && yOffset > 850) {
          scene[0].el.sticky.style.color = "white";
        } else {
          scene[0].el.sticky.style.color = "#292931";
        }
        break;
      case 1:
        break;
      case 2:
        break;
      case 3:
        break;
    }
  };
  window.addEventListener("load", () => {
    setLayout();
    console.log(scene);
  });

  window.addEventListener("scroll", () => {
    yOffset = window.pageYOffset;
    scrollLoop();
  });
</script>

